.scrollToTop {
    position: fixed;
    bottom: 0;
    right: 30px;
    width: 45px;
    height: 45px;
    border-radius: 5px;
    background-color: transparent;
    border-radius: 50%;
    color: #fff;
    line-height: 45px;
    font-size: 20px;
    text-align: center;
    z-index: 9;
    cursor: pointer;
    transition: all 1s;
    transform: translateY(100%);
}

.scrollToTop.active {
    bottom: 30px;
    transform: translateY(0%);
    color: #fff;
}

.scrollToTop:hover{
    color: #fff;
}

.preloader {
    position: fixed;
    width: 100%;
